@import "init";
@import "components/animations";

.page-header {
  .menu {
    a {
      padding:0 35px;
    }
    a.scroll.scroll-active {
      transition-duration: 0.75s;
      color: #ffffff;
    }
  }
}

.profile {
  &.compact {
    padding-bottom: 0;
    &:hover {
      transition-duration: 0.5s;
      box-shadow: $body_color 0 0 0 5px, $body_color 0 0 10px 3px;
      z-index: 1;
      overflow: auto;
      background: #fff;
      font-size: 14px;
      padding-bottom: 15px;
      .field--name-user-picture {
        margin-bottom: 10px;
        &:after {
          background: none;
          margin-top: -10px;
          padding-bottom: 5px;
        }
        img {
          margin-top: 0px;
          padding-bottom: 9px;
          filter: grayscale(0%);
        }
      }

      .field--type-text-with-summary,
      .field--type-text,
      .field--type-link {
        display: block;
      }

      .field--name-field-job-title {
        color: $body_color;
        top: inherit;
        left: 0;
        text-align: left;
        font-style: italic;
      }

      .field--name-field-full-name {
        text-align: left;
        color: $body_color;
        &:after {
          float: left;
          background: none;
          border-top: 1px solid $body_color;
          margin-bottom: 0;
        }
      }
      img {
        transition-property: margin-top, box-shadow;
        transition-duration: 0.5s;
      }
    }
    .field--name-user-picture {
      img {
        transition: 0.5s;
      }
    }
  }
}

.region-about {
  text-align: center;
}

// Latest posts
// Or, view a topic
#block-views-block-blog-view-a-topic-block-1 {
  h2 {
    margin-top: 15px !important;
    margin-bottom: 15px !important;
  }
}

#block-views-block-blog-latest-posts-block-1,
#block-views-block-blog-view-a-topic-block-1 {
  h2 {
    font-family: $pathway_font_stack;
    font-weight: normal;
  }
  .views-row {
    float: left;
    margin: 10px 0;
  }
  .more-link {
    font-family: $pathway_font_stack;
    font-weight: normal;
    font-size: 22px;
    color: $body_color;
    text-decoration: none;
    padding-right: 10px;
    float:right;
  }
}

#block-views-block-blog-latest-posts-block-1 {
  h2 {
    text-transform: uppercase;
  }
  .views-row:last-child {
    display: none;
  }
}

#block-views-block-blog-view-a-topic-block-1 {
  clear: left;
  h2 {
    font-size: 20px;
  }
  form.views-exposed-form {
    border-bottom: 1px solid $body_color;
    margin: 10px 20px 15px 10px;
    .form-item {
      margin: 0;
    }
    ul.views-exposed-filter-tabs-processed {
      list-style: none;
      margin: 0;
      padding: 0;
      li.tab {
        cursor: pointer;
        margin: 0 10px 0 0;
        padding: 2px 10px 0 10px;
        background-color: $body_color;
        color: white;
        float: left;
        font-size: 12px;
        line-height: 22px;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        transition: background-color 0.3s;
        &:hover {
          background-color: #58647e;
        }
        &.active {
          border-top: 1px solid $body_color;
          border-right: 1px solid $body_color;
          border-left: 1px solid $body_color;
          background-color: #fff;
          color: $body_color;
          margin-bottom: -1px;
        }
      }
    }
    #edit-actions {
      display: none;
    }
  }
}

// Blog
article.node--type-blog {
  &.node--view-mode-frontpage {
    .blog-date-link {
      transition: max-height 0.25s;
      max-height: 0;
    }
    &:hover {
      .blog-date-link {
        max-height: 35px;
        transition: max-height 0.25s;
      }
    }
  }
}

#block-views-block-blog-latest-posts-block-1,
#block-views-block-blog-view-a-topic-block-1 {
  .views-row {
    float: left;
    margin: 10px;
  }
}

.ui-widget-overlay {
  background: #000;
  opacity: .5;
  animation: simpleFade50 0.3s ease-out;
}

.ui-dialog {
  max-width: 1003px !important;
  height:80%;
}

// Modal specific styles.
#drupal-modal {
  * {
    animation: simpleFade 0.3s ease-out;
  }

  h2 {
    border-top: 2px solid;
    border-bottom: 2px solid;
    font-family: $pathway_font_stack;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 24px;
  }
  article.node--type-portfolio {
    // min-height:500px;
  }
  .field.field--name-field-site-url {
    display: block;
    $slot_height: 25px;
    @include slot-link($slot_height, 'a', '.slot-link--normal', '.slot-link--hover');
    .slot-link--normal, .slot-link--hover {
      font-family: $museo_font_stack;
      &::after {
        content:      '\e80F';
        font-family:  'fontello';
        font-size:    26px;
        font-weight:  normal;
        padding-left: 10px;
        position:     relative;
        top:          5px;
      }
    }
    .slot-link--hover {
      color: $lastcall_red;
    }
    a {
      float:       right;
      font-family: $museo_font_stack;
      font-size:   12px;
      font-weight: bold;
      outline:     none;
      position:    relative;
      top:         -4px;
    }
  }
}

.view-portfolio,
.view-blog-view-a-topic {
  .view-content {
    // Make each portfolio item fade in one after the other.
    @for $i from 0 through 10 {
      .views-row:nth-child(#{$i}) {
        animation: fadeIn 0.3s * $i ease-out;
      }
    }
  }
}

.node--type-portfolio {
  &.node--view-mode-teaser {
    h2 {
      font-family: $pathway_font_stack;
      font-weight: normal;
      font-size: 25px;
      float: left;
      margin-top: 5px;
      max-width: 260px;
      a {
        text-decoration: none;
        color: #3f444f;
      }
    }
    .field.field--name-title {
      max-width: 150px;
    }
    .field--name-field-cover-image img {
      box-shadow: 0px 2px 10px;
    }
    .portfolio-button {
      float: right;
    }
  }
  &.node--view-mode-full {
    .cycle-slide {
      border: 2px solid $lastcall_grey;
      box-sizing: border-box;
    }
    .node--content {
      font-size: 14px;
      font-family: $museo_font_stack;
      font-weight: 300;
    }
  }
}

.portfolio-button{
  background: $body_color;
  color: #fff;
  text-decoration: none;
  font-family: $pathway_font_stack;
  font-weight: normal;
  text-transform: uppercase;
}

#pane-contact {
  hr.hr-left {
    width: 36%;
    left: 0;
  }
  hr.hr-right {
    width: 36%;
    right: 0;
  }
  ul.leadin {
    li {
      transition: margin-top 0.3s;
      list-style-type: none;
    }
  }
  input[type="submit"], button, a.button {
    background: #f5f5f5;
  }
  input[type="submit"]:hover, button:hover, a.button:hover, a.button:hover {
    background: #3f444f;
  }
}

.page-header #active-indicator {
  width: 50px;
  left: 48%;
  background: #3F444F;
  z-index: -10;
  height: 100%;
}


.region-content {
  .slogan {
    margin: 0 auto;
    font-size: 45px;
    line-height: 1;
    &.top-line {
      width: 440px;
    }
    .small {
      font-size: 20px;
      font-weight: 900;
    }
    .narrow {
      font-weight: 100;
    }
    .wide {
      font-weight: 700;
    }
    .thin{
      font-size: 26px;
    }
    .first-line{
      font-size: 26px;
      background: url("#{$img_path}/Red_Box_Standard.png") no-repeat scroll 2px 15px;
      padding: 20px;
    }
  }
}

#block-portfoliologos {
  h2 {
    font-family: $pathway_font_stack;
    font-weight: normal;
    font-size: 36px;
    text-transform: uppercase;
  }
}


.bg-train {
  right: 93% !important;
  &.train-dislodged-behind {
    animation: dislodgeTrainBehind 0.5s ease-out;
    right: 93.066% !important;
  }
  &.train-dislodged-forward {
    animation: dislodgeTrainForward 0.5s ease-out;
    right: 93% !important;
  }
}
.bg-container .bg-background {
  position: absolute !important;
}

.bg-container {
  .bg-background {
    background-image: url('#{$img_path}/train_background.png');
  }
  .bg-foreground {
    background-image: url('#{$img_path}/train_foreground.png');
  }
  .bg-train {
    background-color: transparent;
    background-image: url('#{$img_path}/train.png');
    background-position: bottom right;
    background-repeat: no-repeat;
  }
  .bg-tracks {
    background-color: transparent;
    background-position: bottom left;
    @include background-image-retina('#{$img_path}/train_tracks', 'png', 31px, 36px);
    background-repeat: repeat-x;
  }
}

.d8-sign {
  @include background-image-retina('#{$img_path}/home_drupal8', 'png', 205px, 109px);
}

body.exception {
  @include background-image-retina('#{$img_path}/train-stop', 'png', 1911px, 405px);
  background-position: center 100%;
  background-repeat: no-repeat;
  .exception-title {
    font-size: 80px;
    border-bottom: 6px solid $body_color;
    text-transform: uppercase;
    font-family: $pathway_font_stack;
  }
  a:hover {
    text-decoration: underline;
  }
  .layout-content {
    background: rgba(245, 245, 245, .75);
  }
}

@include sprites($spritesheet-sprites);
.allaboard-animation {
  display: block;
  position: absolute;
  right: -80px;
  width: 242px;
  height: 263px;
  top: -220px;
}

.bird-animation {
  display: block;
  position: absolute;
  width: 129px;
  height: 196px;
  bottom: 95px;
  left: 19%;
}

.dog-animation {
  display: block;
  position: absolute;
  width: 171px;
  height: 63px;
  bottom: 0;
  left: 24%;
}

.jump-animation {
  width: 315px;
  height: 94px;
  position: absolute;
  left: 43.78%;
  bottom: 0;
}

.surfing-animation {
  display: block;
  width: 140px;
  height: 81px;
  position: absolute;
  right: 100px;
  bottom: 38px;
}

.wave-animation {
  display: block;
  width: 35px;
  height: 31px;
  position: absolute;
  right: 45px;
  top: 15px;
}

.robot-animation {
  display: block;
  width: 502px;
  height: 222px;
  position: absolute;
  left: 88.5%;
  bottom: 5px;
  z-index: 50;

  &.animation-animation_robot-standard05 {
    width: 361px;
  }
  &.animation-animation_robot-standard06 {
    width: 361px;
  }
  &.animation-animation_robot-standard07 {
    width: 323px;
  }
  &.animation-animation_robot-standard08 {
    width: 274px;
  }
  &.animation-animation_robot-standard09 {
    width: 243px;
  }
  &.animation-animation_robot-standard10 {
    width: 217px;
  }
  &.animation-animation_robot-standard11 {
    width: 215px;
  }
  &.animation-animation_robot-standard11 {
    width: 203px;
  }
  &.animation-animation_robot-standard12 {
    width: 200px;
  }
}

.animation-hidden {
  height: 0 !important;
}
